*,
:before,
:after {
    box-sizing: border-box;
}

html {
    tab-size: 4;
    -webkit-text-size-adjust: 100%;
    line-height: 1.15;
}

body {
    margin: 0;
    font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
    height: 0;
    color: inherit;
}

abbr[title] {
    -webkit-text-decoration: underline dotted;
    text-decoration: underline dotted;
}

b,
strong {
    font-weight: bolder;
}

code,
kbd,
samp,
pre {
    font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
    font-size: 1em;
}

small {
    font-size: 80%;
}

sub,
sup {
    vertical-align: baseline;
    font-size: 75%;
    line-height: 0;
    position: relative;
}

sub {
    bottom: -.25em;
}

sup {
    top: -.5em;
}

table {
    text-indent: 0;
    border-color: inherit;
}

button,
input,
optgroup,
select,
textarea {
    margin: 0;
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
}

button,
select {
    text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
    -webkit-appearance: button;
}

::-moz-focus-inner {
    border-style: none;
    padding: 0;
}

:-moz-focusring {
    outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
    box-shadow: none;
}

legend {
    padding: 0;
}

progress {
    vertical-align: baseline;
}

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
    height: auto;
}

[type=search] {
    -webkit-appearance: textfield;
    outline-offset: -2px;
}

::-webkit-search-decoration {
    -webkit-appearance: none;
}

::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit;
}

.summary {
    display: list-item;
}

.example {
    color: #2f2f37;
    max-width: 960px;
    background-color: #fff;
    padding: 16px;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
    line-height: 1.5;
}

.example code {
    color: #fff;
    background-color: #ff6b0a;
    border-radius: 2px;
    padding: 2px 6px;
    font-family: monospace;
    font-size: 16px;
    font-weight: 700;
}

.example .background {
    height: 240px;
    background-position: 50%;
    background-size: cover;
}

.search-form {
    text-align: center;
    width: 100%;
    background-color: blue;
    margin-bottom: 15px;
    padding-top: 5px;
    padding-bottom: 5px;
    position: fixed;
    top: 0;
}

.gallery {
    flex-wrap: wrap;
    gap: 15px;
    margin-top: 30px;
    padding-top: 15px;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
}

img {
    width: 100%;
    height: 75%;
    display: block;
}

.img-card {
    width: calc(25% - 11.25px);
    height: auto;
    color: #000;
    flex-direction: column;
    text-decoration: none;
    display: flex;
}

.about {
    text-align: center;
    border-bottom: 1px solid gray;
    border-left: 1px solid gray;
    border-right: 1px solid gray;
    flex-grow: 1;
    justify-content: space-around;
    display: flex;
}

.about-item {
    margin-bottom: 10px;
}

.load-more {
    border: none;
    margin: 15px auto;
    padding: 5px;
    display: block;
}

.load-more:active {
    background-color: blue;
}

.load-more.hidden {
    clip: rect(0 0 0 0);
    -webkit-clip-path: inset(50%);
    clip-path: inset(50%);
    height: 1px;
    white-space: nowrap;
    width: 1px;
    position: absolute;
    overflow: hidden;
}

body.hidden-scroll {
    overflow: hidden;
}

.sl-overlay {
    z-index: 1035;
    background: #fff;
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.sl-wrapper {
    z-index: 1040;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
}

.sl-wrapper * {
    box-sizing: border-box;
}

.sl-wrapper button {
    cursor: pointer;
    background: 0 0;
    border: 0;
    padding: 0;
    font-size: 28px;
}

.sl-wrapper button:hover {
    opacity: .7;
}

.sl-wrapper .sl-close {
    z-index: 10060;
    height: 44px;
    width: 44px;
    color: #000;
    margin-top: -14px;
    margin-right: -14px;
    font-family: Arial, Baskerville, monospace;
    font-size: 3rem;
    line-height: 44px;
    display: none;
    position: fixed;
    top: 30px;
    right: 30px;
}

.sl-wrapper .sl-counter {
    z-index: 1060;
    color: #000;
    font-size: 1rem;
    display: none;
    position: fixed;
    top: 30px;
    left: 30px;
}

.sl-wrapper .sl-navigation {
    width: 100%;
    display: none;
}

.sl-wrapper .sl-navigation button {
    height: 44px;
    width: 22px;
    text-align: center;
    z-index: 10060;
    color: #000;
    margin-top: -22px;
    font-family: Arial, Baskerville, monospace;
    line-height: 44px;
    display: block;
    position: fixed;
    top: 50%;
}

.sl-wrapper .sl-navigation button.sl-next {
    font-size: 2rem;
    right: 5px;
}

.sl-wrapper .sl-navigation button.sl-prev {
    font-size: 2rem;
    left: 5px;
}

@media (min-width: 35.5em) {
    .sl-wrapper .sl-navigation button {
        width: 44px;
    }

    .sl-wrapper .sl-navigation button.sl-next {
        font-size: 3rem;
        right: 10px;
    }

    .sl-wrapper .sl-navigation button.sl-prev {
        font-size: 3rem;
        left: 10px;
    }
}

@media (min-width: 50em) {
    .sl-wrapper .sl-navigation button {
        width: 44px;
    }

    .sl-wrapper .sl-navigation button.sl-next {
        font-size: 3rem;
        right: 20px;
    }

    .sl-wrapper .sl-navigation button.sl-prev {
        font-size: 3rem;
        left: 20px;
    }
}

.sl-wrapper.sl-dir-rtl .sl-navigation {
    direction: ltr;
}

.sl-wrapper .sl-image {
    -ms-touch-action: none;
    touch-action: none;
    z-index: 10000;
    position: fixed;
}

.sl-wrapper .sl-image img {
    width: 100%;
    height: auto;
    border: 0;
    margin: 0;
    padding: 0;
    display: block;
}

@media (min-width: 35.5em) {
    .sl-wrapper .sl-image img {
        border: 0;
    }
}

@media (min-width: 50em) {
    .sl-wrapper .sl-image img {
        border: 0;
    }
}

.sl-wrapper .sl-image iframe {
    background: #000;
    border: 0;
}

@media (min-width: 35.5em) {
    .sl-wrapper .sl-image iframe {
        border: 0;
    }
}

@media (min-width: 50em) {
    .sl-wrapper .sl-image iframe {
        border: 0;
    }
}

.sl-wrapper .sl-image .sl-caption {
    color: #fff;
    background: rgba(0, 0, 0, .8);
    padding: 10px;
    font-size: 1rem;
    display: none;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}

.sl-wrapper .sl-image .sl-caption.pos-top {
    top: 0;
    bottom: auto;
}

.sl-wrapper .sl-image .sl-caption.pos-outside {
    bottom: auto;
}

.sl-wrapper .sl-image .sl-download {
    color: #000;
    z-index: 1060;
    display: none;
    position: absolute;
    bottom: 5px;
    right: 5px;
}

.sl-spinner {
    height: 40px;
    opacity: 0;
    width: 40px;
    z-index: 1007;
    -ms-animation: pulsate 1s ease-out infinite;
    border: 5px solid #333;
    border-radius: 40px;
    margin: -20px 0 0 -20px;
    animation: pulsate 1s ease-out infinite;
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
}

.sl-scrollbar-measure {
    width: 50px;
    height: 50px;
    position: absolute;
    top: -9999px;
    overflow: scroll;
}

.sl-transition {
    transition: transform .2s;
}

@keyframes pulsate {
    0% {
        opacity: 0;
        transform: scale(.1);
    }

    50% {
        opacity: 1;
    }

    to {
        opacity: 0;
        transform: scale(1.2);
    }
}


